// frontend/src/components/MessageExportPanel.js
import { useState } from 'react';
import { FaTimes, FaDownload, FaFileExport, FaCalendar } from 'react-icons/fa';
import toast from '../utils/toast';

/**
 * ðŸ“¦ Message Export Panel
 * Sohbet geÃ§miÅŸini dÄ±ÅŸa aktarma
 */

const MessageExportPanel = ({ fetchWithAuth, apiBaseUrl, roomSlug, onClose }) => {
    const [format, setFormat] = useState('json');
    const [dateRange, setDateRange] = useState({
        start_date: '',
        end_date: ''
    });
    const [includeAttachments, setIncludeAttachments] = useState(false);
    const [processing, setProcessing] = useState(false);
    const [jobId, setJobId] = useState(null);
    const [downloadUrl, setDownloadUrl] = useState(null);

    const formats = [
        { value: 'json', label: 'JSON', icon: 'ðŸ“‹' },
        { value: 'csv', label: 'CSV (Excel)', icon: 'ðŸ“Š' },
        { value: 'txt', label: 'Text', icon: 'ðŸ“„' },
        { value: 'html', label: 'HTML', icon: 'ðŸŒ' }
    ];

    const startExport = async () => {
        try {
            setProcessing(true);
            const response = await fetchWithAuth(`${apiBaseUrl}/exports/create/`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    room_slug: roomSlug,
                    format,
                    start_date: dateRange.start_date || null,
                    end_date: dateRange.end_date || null,
                    include_attachments: includeAttachments
                })
            });

            if (response.ok) {
                const data = await response.json();
                setJobId(data.job_id);
                toast.success('DÄ±ÅŸa aktarma baÅŸlatÄ±ldÄ±');
                checkExportStatus(data.job_id);
            } else {
                toast.error('DÄ±ÅŸa aktarma baÅŸlatÄ±lamadÄ±');
                setProcessing(false);
            }
        } catch (error) {
            console.error('Export baÅŸlatma hatasÄ±:', error);
            toast.error('Bir hata oluÅŸtu');
            setProcessing(false);
        }
    };

    const checkExportStatus = async (jId) => {
        const interval = setInterval(async () => {
            try {
                const response = await fetchWithAuth(`${apiBaseUrl}/exports/${jId}/status/`);
                if (response.ok) {
                    const data = await response.json();

                    if (data.status === 'completed') {
                        clearInterval(interval);
                        setProcessing(false);
                        setDownloadUrl(data.download_url);
                        toast.success('DÄ±ÅŸa aktarma tamamlandÄ±!');
                    } else if (data.status === 'failed') {
                        clearInterval(interval);
                        setProcessing(false);
                        toast.error('DÄ±ÅŸa aktarma baÅŸarÄ±sÄ±z oldu');
                    }
                }
            } catch (error) {
                clearInterval(interval);
                setProcessing(false);
                console.error('Status kontrol hatasÄ±:', error);
            }
        }, 2000);
    };

    const downloadExport = () => {
        if (downloadUrl) {
            window.open(downloadUrl, '_blank');
            toast.success('Ä°ndirme baÅŸlatÄ±ldÄ±');
        }
    };

    return (
        <div style={styles.overlay} onClick={onClose}>
            <div style={styles.modal} onClick={(e) => e.stopPropagation()}>
                <div style={styles.header}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                        <FaFileExport style={{ color: '#43b581' }} />
                        <h2 style={{ margin: 0 }}>Sohbet GeÃ§miÅŸini DÄ±ÅŸa Aktar</h2>
                    </div>
                    <FaTimes onClick={onClose} style={styles.closeBtn} />
                </div>

                <div style={styles.content}>
                    {!downloadUrl ? (
                        <>
                            <div style={styles.field}>
                                <label style={styles.label}>
                                    <FaDownload style={{ marginRight: '8px' }} />
                                    Dosya FormatÄ±
                                </label>
                                <div style={styles.formatGrid}>
                                    {formats.map((fmt) => (
                                        <button
                                            key={fmt.value}
                                            onClick={() => setFormat(fmt.value)}
                                            style={{
                                                ...styles.formatBtn,
                                                backgroundColor: format === fmt.value ? '#5865f2' : '#2c2f33'
                                            }}
                                        >
                                            <span style={{ fontSize: '24px' }}>{fmt.icon}</span>
                                            <span>{fmt.label}</span>
                                        </button>
                                    ))}
                                </div>
                            </div>

                            <div style={styles.field}>
                                <label style={styles.label}>
                                    <FaCalendar style={{ marginRight: '8px' }} />
                                    Tarih AralÄ±ÄŸÄ± (opsiyonel)
                                </label>
                                <div style={styles.dateRange}>
                                    <input
                                        type="date"
                                        value={dateRange.start_date}
                                        onChange={(e) => setDateRange({ ...dateRange, start_date: e.target.value })}
                                        style={styles.dateInput}
                                        placeholder="BaÅŸlangÄ±Ã§"
                                    />
                                    <span style={{ color: '#888' }}>â†’</span>
                                    <input
                                        type="date"
                                        value={dateRange.end_date}
                                        onChange={(e) => setDateRange({ ...dateRange, end_date: e.target.value })}
                                        style={styles.dateInput}
                                        placeholder="BitiÅŸ"
                                    />
                                </div>
                            </div>

                            <div style={styles.field}>
                                <label style={styles.checkboxLabel}>
                                    <input
                                        type="checkbox"
                                        checked={includeAttachments}
                                        onChange={(e) => setIncludeAttachments(e.target.checked)}
                                    />
                                    <span>Ekleri de dahil et (dosya boyutu artabilir)</span>
                                </label>
                            </div>

                            {processing && (
                                <div style={styles.progress}>
                                    <div style={styles.progressSpinner}></div>
                                    <p>Mesajlar iÅŸleniyor...</p>
                                    <p style={{ fontSize: '12px', color: '#888' }}>Bu iÅŸlem birkaÃ§ dakika sÃ¼rebilir</p>
                                </div>
                            )}
                        </>
                    ) : (
                        <div style={styles.success}>
                            <div style={styles.successIcon}>âœ…</div>
                            <h3>DÄ±ÅŸa Aktarma TamamlandÄ±!</h3>
                            <p>DosyanÄ±z hazÄ±r ve indirilebilir durumda.</p>
                            <button onClick={downloadExport} style={styles.downloadBtn}>
                                <FaDownload /> DosyayÄ± Ä°ndir
                            </button>
                        </div>
                    )}
                </div>

                {!downloadUrl && (
                    <div style={styles.footer}>
                        <button onClick={onClose} style={styles.cancelBtn}>
                            Ä°ptal
                        </button>
                        <button
                            onClick={startExport}
                            disabled={processing}
                            style={styles.exportBtn}
                        >
                            {processing ? 'Ä°ÅŸleniyor...' : 'DÄ±ÅŸa Aktar'}
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

